<template lang="pug">
    .share-user(ref="shareUser")
        .backgroundOverlay(@click="closeShare")
        
        .users-default(v-if="usersDefaultShare.length")
            we-userThumb(v-for="user in usersDefaultShare", :data="user")

        .users(v-if="tempSelectUser.length")
            we-userThumb(v-for="user in tempSelectUser", :data="user")
        
        .wrapper-share-user
            button.button-share-user(@click="openShare")
            input.input-text(placeholder="Buscar")

            .list-search
                .user(v-for="user in this.users", @click="selectUser(user)")
                    .photo(:style="{'background-image':'url('+user.infos.photo+')'}")
                    .infos
                        span.name {{user.infos.name}}
                        span.count-task 2 task

</template>

<script>
    import WeUserThumb from '@/components/common/We-UserThumb.vue'
    import Users from '@/users.js'

    export default {
        name: 'w-shareUser',
        props: {
            data: Array
        },
        components:{
            'we-userThumb': WeUserThumb
        },
        data () {
            return {
                users: Users.users,
                tempSelectUser: [],
                usersDefaultShare: [],
                msg: 'Welcome to Your Vue.js App'
            }
        },
        watch: {
            tempSelectUser(data){
                this.$emit('selectUsers', data);
            }
        },
        created(){
            this.getCurrentUsersShare();
        },
        methods: {
            stop(){},
            getCurrentUsersShare(){
                if (this.data) {
                    for (var i = 0; i < this.data.length; i++) {
                        if (!this.data[i].master) {
                            this.usersDefaultShare.push(this.data[i])
                        }
                    }
                }
            },
            selectUser(user){
                this.tempSelectUser.push(user);
            },
            openShare(){
                this.$refs.shareUser.classList.add('show');
            },
            closeShare(){
                this.$refs.shareUser.classList.remove('show');
            }
        },
        beforeDestroy(){
            // this.tempSelectUser = [];
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/ShareUser.styl"></style>


